.form-control:focus {
    border-color: #DC3544 !important;
    box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, .5) !important;
}

.sort-button:active {
    background-color: #dee2e6 !important;
}

.sort-button:hover {
    cursor: pointer;
}

.font-awesome-button {
    background: transparent;
    border: none;
}

.font-awesome-button:focus {
    outline: none;
}

#map1 {
    width: 100%;
    height: 600px;
}

.multiselect div {
    border-color: #cccccc !important;
    color: #6c757d;
    box-shadow: none;
}

.multiselect div:hover {
    border-color: #cccccc !important;
    color: #6c757d;
    box-shadow: none;
}

.multiselect_required div {
    border-color: #DC3544 !important;
    color: #6c757d;
    box-shadow: none;
}

.multiselect_required div:hover {
    border-color: #DC3544 !important;
    color: #6c757d;
    box-shadow: none;
}

.delete_button {
    padding-top: 2px !important;
}

.rct-item-content {
    width: 100%;
}

.react-calendar-timeline .rct-header-root {
    background: #be3d3d !important;
}

.react-calendar-timeline .rct-sidebar .rct-sidebar-row.rct-sidebar-row-even {
    padding: 0;
}

.react-calendar-timeline .rct-sidebar .rct-sidebar-row.rct-sidebar-row-odd {
    padding: 0;
}

.custom-group:active {
    background: #be3d3d;
    color: white;
}

.custom-group {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.rct-item {
    user-select: none;
}

.fas.fa-times {
    font-size: 25px;
}