.loadAnimation {
    /*выравнивание на середину*/
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    /*анимация*/
    -webkit-animation-name: rotation;
            animation-name: rotation; /*назвение*/
    -webkit-animation-duration: 10s;
            animation-duration: 10s; /*Задаем продолжительность одного цикла анимации.*/
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite; /*Задаем количество повторений цикла, в нашем случае — бесконечность..*/
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear; /*Ну и указываем плавный тип анимации..*/
}

@-webkit-keyframes rotation { /*Создаем ключевые кадры для анимации с именем «rotation».*/
    /*Старт цикла, картинка повернута на 0 градусов о.О*/
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    /*Конец цикла, картинка повернута на 360 градусов, т.е. ее полный оборот.*/
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotation { /*Создаем ключевые кадры для анимации с именем «rotation».*/
    /*Старт цикла, картинка повернута на 0 градусов о.О*/
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    /*Конец цикла, картинка повернута на 360 градусов, т.е. ее полный оборот.*/
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

